import React from 'react';

import NavBar from '../Common/NavBar';
import buttons from './text/buttons';
import '../../styles/content.css';

class About extends React.Component {
    state = {
        currentView: 0,
        prevView: 0,
        count: 0,
    }

    onButtonClick = (index) => {
        console.log(index)
        if (index !== this.state.currentView) {
            this.setState({currentView: index, prevView: this.state.currentView});
        }
    }

    renderView() {
        if (this.state.prevView === "") {
            return (
                <div className="front">
                    {buttons[this.state.currentView].content}
                </div>
            )
        } else {
            return (
                <div>
                    <div className="front" style={{display: "none"}}>
                        {buttons[this.state.prevView].content}
                    </div>
                    <div className="left">
                        {buttons[this.state.currentView].content}
                    </div>
                </div>
            )
        }
    }

    render() {
        return (
            <div className="content solid-border">
                <div className="hello">
                    <h1>Cześć jestem Magda!</h1>
                    <p>Zapraszam do zapoznania się z moimi projektami.</p>
                </div>
                <div className="nav-bar">
                    <NavBar buttons={buttons} onButtonClick={this.onButtonClick} />
                </div>
                <div className="rotating">
                    <div className="conteiner-box">
                        <div className="box">
                            {this.renderView()} 
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default About;