import React from 'react';
import '../../styles/navbar.css';

const NavBar = (props) => {
    return props.buttons.map((button, index) => {
        return <button 
            key={button.text} 
            type="button"
            id={index} 
            onClick = {() => {props.onButtonClick(index)}}>{button.text}</button>
    })
}

export default NavBar;