import React from 'react';


import { tableOfProgramingLang } from './text/aboutText';
import SkilsReckord from './SkilsRecord';

class Programing extends React.Component {
    state = {
        ang: false,
    }

    renderPl() {
        return (
            <div className="about table-conteiner">
                <table>
                    <thead>
                        <tr>
                            <th>Nazwa</th>
                            <th>Stars</th>
                            <th>Github</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.renderSkills()}
                    </tbody>
                </table>  
            </div>
        )
    }

    renderSkills() {
        return tableOfProgramingLang.map(( skill ) => {
            return <SkilsReckord key={skill.name} skill = {skill} />
        })

    }

    renderEn() {
        return (
            <div className="about">
                Hello! My name is Magda!
            </div>
        )
    }

    render() {    
        return this.state.lang ? this.renderEn() : this.renderPl();
    }
}

export default Programing;